.coverpage{
    width: 100%;
    height: 100%;
    text-align:center;
    background-position: center;
    position: absolute;
    background-color: rgb(122, 166, 218);
    background-image: url(background3.jpeg);
    opacity: 90%;

 
}
.form1{
    width: 50%;
    height: 70%;
    background-image: url(background3.jpeg);
    
    top: 5%;
    left: 5%;
    position: absolute;
 padding: 30px 10px;
}
.form1 input{
    width: 100%;
    margin-bottom: 10px;
}
.form1 button{
    width: 100%;
    margin-bottom: 20px;
    
}
.form1 input[type="text"]{
    border: none;
    border-bottom: 1px solid rgb(66, 61, 61);
    background: transparent;
    outline: none; 
    height: 40px;
    color: rgb(233, 241, 245);
    font-size: 16px;
}
.form1 button[type="submit"]{

    height: 40px;
    background-color: rgb(50, 77, 196);
    color: rgb(255, 253, 253);
    font-size: 15px;
    text-decoration-style: solid;
    text-align: center;
    

   
    /*left: 20px;*/
}
.buttonDelete{
    border:none;
    outline: none;
    height: 40px;
    background: rgb(165, 5, 5);
    color: rgb(238, 228, 228);
    font-style: inherit;
   /*left: 20px; */ 
}
